export default {
	data() {
		return {
			codeMsg: '发送验证码',
			count: 10,
			disable: false
		};
	},
	methods:{
		clickCodemsg(){
			if(this.disable == false){
				let timer =  setInterval(() => {
					this.count -= 1
					this.codeMsg = this.count + 's'
					this.disable = true
					if(this.count < 0){
						clearInterval(timer)
						this.codeMsg = '发送验证码'
						this.count = 10
						this.disable = false
					}
				}, 1000)
			}
		},
		gotoUrl(){
			uni.navigateBack({
				delta:1
			})
		}
	}
}
